import React from 'react';
import PropTypes from 'prop-types';
import { Panel, Group, PanelHeader, Cell } from '@vkontakte/vkui';
import PollModel from "../models/PollModel";
import SCList from "../blocks/SCList";


export default class Polls extends React.Component {
    makeModels() {
        return [
            new PollModel({title: "  ", author: ""}),
            new PollModel({title: " ", author: "VKmusic"}),
            new PollModel({title: " VS ", author: "  "}),
            new PollModel({title: "  2018", author: ""}),
            new PollModel({title: "    GQ", author: "GQ"})
        ];
    }

    render() {
        const models = this.makeModels();
        let cells = models.map(item =>
            <Cell description={item.author}>{item.title}</Cell>
        );
        return (
            <Panel id={this.props.id}>
                <PanelHeader></PanelHeader>
                <Group title="">
                    <SCList cells={cells}/>
                </Group>
                <Group title="">
                    <SCList cells={cells}/>
                </Group>
            </Panel>
        );
    }
}

Polls.propTypes = {
    id: PropTypes.string.isRequired
};



